/* src/components/TaskProgress.css */

.task-progress {
    display: flex;
    align-items: center;
    margin-top: 10px;
}

.progress-container {
    display: flex;
    align-items: center;
    width: 100%;
}

.progress-bar {
    width: 100%;
    height: 20px;
    margin-right: 10px;
}

.progress-text {
    font-weight: bold;
}

.progress-indicator {
    height: 10px;
    width: 100%;
    border-radius: 5px;
    margin-top: 5px;
}
