/* src/components/Tasks/TaskList.css */
.task-list-container {
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.task-list-container h1 {
    margin-bottom: 20px;
    font-size: 1.8rem;
    color: #333;
}

.filters {
    margin-bottom: 20px;
}

.filters label {
    margin-right: 10px;
}

.filters input,
.filters select {
    margin-right: 10px;
}

.filters button {
    padding: 5px 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.filters button:hover {
    background-color: #0056b3;
}

.loading {
    text-align: center;
    font-size: 1.2rem;
    color: #007bff;
}

.error-message {
    color: red;
    margin-bottom: 10px;
}

.task-table {
    width: 100%;
    border-collapse: collapse;
}

.task-table th,
.task-table td {
    border: 1px solid #ddd;
    padding: 8px;
}

.task-table th {
    background-color: #f2f2f2;
    text-align: left;
}

.pagination {
    margin-top: 20px;
    text-align: center;
}

.pagination button {
    padding: 5px 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin: 0 5px;
}

.pagination button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
}

.pagination select {
    margin-left: 10px;
}
