/* src/components/Layout/Navbar.css */
.navbar {
  background-color: #333;
  padding: 10px;
  color: white;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-title {
  font-size: 1.5rem;
  margin: 0;
}

.navbar-menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.navbar-item {
  margin-left: 20px;
}

.navbar-item a {
  color: white;
  text-decoration: none;
  font-size: 1rem;
}

.navbar-item a:hover {
  text-decoration: underline;
}

.navbar-item.active a {
  font-weight: bold;
  border-bottom: 2px solid #f39c12;
}

@media (max-width: 768px) {
  .navbar-menu {
    flex-direction: column;
  }

  .navbar-item {
    margin: 10px 0;
  }
}
