/* src/components/Tasks/TaskForm.css */
.task-form {
    padding: 20px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    max-width: 500px;
    margin: 0 auto;
}

.task-form h2 {
    margin-bottom: 20px;
    font-size: 1.8rem;
    color: #333;
}

.task-form label {
    display: block;
    margin-bottom: 15px;
    font-size: 1rem;
    color: #555;
}

.task-form input,
.task-form select {
    width: 100%;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
    margin-top: 5px;
}

.task-form button {
    padding: 10px 15px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
    margin-top: 10px;
}

.task-form button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
}

.error-message {
    color: red;
    margin-bottom: 15px;
    font-size: 0.9rem;
}
